/* src/design/TeamPage.css */

.team-page {
  padding: 60px 20px;
  text-align: center;
  background-color: #f5f8fa;
  font-family: 'Segoe UI', sans-serif;
}

.team-title {
  font-size: 40px;
  margin-bottom: 10px;
  color: #2c3e50;
}

.team-subtitle {
  font-size: 18px;
  color: #7f8c8d;
  margin-bottom: 40px;
}

.team-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
}

.team-card {
  background-color: #ffffff;
  border-radius: 16px;
  box-shadow: 0 10px 24px rgba(0, 0, 0, 0.08);
  padding: 25px;
  width: 280px;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.team-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 18px 36px rgba(0, 0, 0, 0.15);
}

.team-img {
  width: 110px;
  height: 110px;
  border-radius: 50%;
  object-fit: cover;
  margin-bottom: 18px;
  border: 3px solid #3498db;
  transition: transform 0.3s ease;
}

.team-card:hover .team-img {
  transform: scale(1.05);
}

.team-card h3 {
  font-size: 22px;
  color: #2c3e50;
  margin: 10px 0 5px;
}

.role {
  color: #3498db;
  font-size: 14px;
  font-weight: 600;
  margin-bottom: 10px;
}

.bio {
  font-size: 14px;
  color: #555;
  margin-bottom: 15px;
  padding: 0 5px;
  line-height: 1.5;
  min-height: 60px;
}

.social-links {
  display: flex;
  justify-content: center;
  gap: 15px;
  margin-top: auto;
}

.social-links a {
  color: #555;
  font-size: 18px;
  transition: color 0.3s ease, transform 0.3s ease;
}

.social-links a:hover {
  color: #3498db;
  transform: scale(1.2);
}

@media (max-width: 768px) {
  .team-grid {
    flex-direction: column;
    align-items: center;
  }
}
