.privacy-policy-container {
  max-width: 900px;
  margin: 50px auto;
  padding: 30px;
  background-color: #fff;
  border-radius: 10px;
  font-family: 'Segoe UI', sans-serif;
  line-height: 1.8;
  box-shadow: 0 0 12px rgba(0, 0, 0, 0.05);
  color: #333;
}

.privacy-policy-container h1 {
  text-align: center;
  font-size: 36px;
  margin-bottom: 30px;
  color: #0b3d91;
}

.privacy-policy-container h2 {
  margin-top: 30px;
  font-size: 22px;
  color: #222;
}

.privacy-policy-container ul {
  list-style: disc;
  margin-left: 20px;
  margin-top: 10px;
}

.privacy-policy-container p {
  margin-bottom: 16px;
}
