.carousel {
  position: relative;
  width: 100%;
  overflow: hidden;
  background: #000; /* fallback background */
  display: flex;
  justify-content: center;
  align-items: center;
  max-height: 500px;
}

.carousel-slide {
  opacity: 0;
  transition: opacity 1s ease-in-out;
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.carousel-slide.active {
  opacity: 1;
  position: relative;
}

.carousel-slide img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain; /* âœ… Prevent cropping */
  display: block;
}

/* Caption styling */


/* Responsive adjustments */
@media (max-width: 768px) {
  .carousel {
    max-height: 250px;
  }

  
}

@media (max-width: 480px) {
  .carousel {
    max-height: 200px;
  }

  
}
