/* Brand Card Container */
.brand-card {
  background: #fff;
  border: 1px solid #eee;
  border-radius: 8px;
  padding: 15px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
  transition: transform 0.3s ease, box-shadow 0.3s ease, border-color 0.3s ease;
  cursor: pointer;
}

/* Hover effect: lift + highlight */
.brand-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 10px 24px rgba(217, 0, 0, 0.2); /* red-tinted glow */
  border-color: #d90000;
}

/* Image Container */
.brand-image-container {
  width: 100%;
  height: 180px; /* rectangular shape */
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
  background-color: #f7f7f7;
  border: 1px solid #ddd;
  border-radius: 4px;
  overflow: hidden;
}

/* Image inside card */
.brand-image-container img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
  transition: transform 0.3s ease;
}

/* Zoom image slightly on hover */
.brand-card:hover .brand-image-container img {
  transform: scale(1.05);
}

/* Brand Info Text */
.brand-info {
  margin-top: 10px;
  font-size: 14px;
}

.brand-info h4 {
  margin: 5px 0;
  font-size: 16px;
  font-weight: 600;
  color: #333;
}

.brand-info p {
  margin: 3px 0;
  color: #666;
}

/* Know More Button */
.know-more {
  margin-top: 10px;
  text-align: center;
}

.know-more button {
  background: transparent;
  color: #d90000;
  border: 1px solid #d90000;
  padding: 6px 14px;
  font-size: 14px;
  border-radius: 4px;
  cursor: pointer;
  transition: 0.3s ease;
}

.know-more button:hover {
  background: #d90000;
  color: white;
}
.floating-advice-button-inline {
  background-color: #d90000;
  color: white;
  font-weight: 600;
  padding: 8px 16px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: 0.3s ease;
}

.floating-advice-button-inline:hover {
  background-color: #b30000;
}

/* Modal */
.advice-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.4);
  z-index: 9998;
  display: flex;
  justify-content: flex-end;
  animation: fadeIn 0.3s ease;
}

.advice-modal {
  background: #fff;
  width: 100%;
  max-width: 400px;
  height: 100%;
  padding: 30px 20px;
  position: relative;
  animation: slideInRight 0.4s ease;
  overflow-y: auto;
  z-index: 9999;
}

.advice-close {
  position: absolute;
  top: 10px;
  right: 14px;
  background: none;
  border: none;
  font-size: 24px;
  color: #d90000;
  cursor: pointer;
}

.advice-modal h3 {
  margin-top: 30px;
  margin-bottom: 10px;
  color: #333;
}

.advice-modal p {
  margin-bottom: 20px;
  color: #555;
}

.advice-form input,
.advice-form textarea {
  width: 100%;
  padding: 10px;
  margin-bottom: 14px;
  border: 1px solid #ccc;
  border-radius: 6px;
  font-size: 14px;
}

.advice-form button {
  width: 100%;
  padding: 10px;
  background: #d90000;
  color: white;
  border: none;
  font-size: 16px;
  border-radius: 6px;
  cursor: pointer;
  transition: background 0.3s ease;
}

.advice-form button:hover {
  background: #b30000;
}

/* Animations */
@keyframes slideInRight {
  from {
    transform: translateX(100%);
  }
  to {
    transform: translateX(0);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
